@charset "utf-8";

@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

body {
    font-family: 'Pretendard-Regular';
}

*,
*:after,
*::before {
    box-sizing: border-box;
}

a {
    color: black;
    text-decoration: none;
}

#header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 70px;
    z-index: 1000;
    transition: all .6s ease;
}

#header:hover, .header_hover {
    box-shadow: 0px 1px 5px 0px gray;
    background: rgba(255, 255, 255, 0.95)
}

.inner {
    position: relative;
    width: 80%;
    height: 70px;
    margin: 0 auto;
}

.inner h1 img {
    width: 160px;
    margin-top: 10px;
}

/* 검색 */
form {
    position: absolute;
    right: 0;
    top: 20px;
    height: 30px;
    z-index: 100;
}

.form_search_input {
    width: 0;
    height: 30px;
    border: none;
    outline: none;
    transition: width 0.5s;
    background: transparent;
}

.search_input_open {
    width: 150px;
    outline: none;
    border: 1px solid #9c9c9c;
    background: #fff;
    height: 30px;
}

.form_search_submit {
    display: inline-block;
    width: 30px;
    height: 30px;
}

.form_search_submit img {
    width: 30px;
    height: 30px;
    line-height: 70px;
    vertical-align: middle;
}

.menu {
    width: 100%;
}

.menubox {
    width: 80%;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
}

.menubox .nav {
    width: 60%;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
}

.menubox .nav>li {
    width: 100%;
    text-align: center;
}

.menubox .nav>li>a {
    display: block;
    height: 70px;
    line-height: 70px;
    font-size: 1.1rem;
    font-weight: bold;
}

/* 메뉴보이게 */
.sub {
    display: none;
}

.sub li a {
    display: block;
    height: 45px;
    line-height: 45px;
    font-weight: 500;
}

.sub li a:hover {
    color: #3CC3B2;
    font-weight: bold;
}

/* 메뉴 배경 보이게 */
.subbox {
    position: absolute;
    top: 70px;
    left: 0;
    width: 100%;
    height: 400px;
    background: rgba(255, 255, 255, 0.9);
    z-index: -1;
    display: none;
}

/* 메인시작 */
#main {
    position: relative;
    width: 100%;
}

#main img {
    width: 100%;
}

.slick-slide {
    position: relative;
}

.main_text {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 15%;
    width: 80%;
}

.main_txt {
    margin-bottom: 2rem;
    color: #fff;
}

.main_txt li:nth-child(1) {
    font-size: 3.5rem;
    letter-spacing: 2px;
    margin-bottom: 0.5rem;
}

.main_txt li:nth-child(2) {
    line-height: 1.7;
    letter-spacing: 1.2px;
}

.main_text p {
    width: 150px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border: 1px solid #fff;
    color: #fff;
}

.main_text p:hover {
    background: #fff;
    color: #3CC3B2;
    font-weight: bold;
}

/* 메인끝 */

/* NEW PRODUCT시작 */
#content {
    width: 80%;
    margin: 8rem auto 0;
    position: relative;
}

hgroup {
    text-align: center;
    margin-bottom: 4rem;
}

hgroup h1 {
    font-size: 2.2rem;
    margin-bottom: 1.5rem;
}

#content p>a, #news p>a {
    display: block;
    border: 1px solid #000;
    width: 150px;
    margin: 3.5rem auto 0;
    height: 40px;
    line-height: 40px;
    text-align: center;
    transition: all 0.4s ease;
}

#content p>a:hover, #news p>a:hover {
    border: 1px solid #3CC3B2;
    background: #3CC3B2;
    color: #fff;
}

/* NEW PRODUCT 끝 */

/* video_box 시작 */
#video_box {
    position: relative;
    width: 80%;
    margin: 8rem auto 0;
    display: flex;
    justify-content: end;
    background: rgba(60, 195, 178, 0.7);
    border-radius: 40px 0 40px 0;
}

#video_box div:nth-child(1) {
    position: absolute;
    bottom: -15px;
    left: 0;
}

#video_box div:nth-child(1) img {
    width: 100%;
    vertical-align: bottom;
    animation: img 0.8s ease-in 1s infinite alternate;
}

@keyframes img {
    0% {
      transform: translateY(0);
    }
  
    100% {
      transform: translateY(-15px);
    }
  }

#video_box p {
    position: absolute;
    left: 10%;
    top: 35%;
    font: italic bold 2rem'Pretendard-Regular';
    line-height: 1.5;
    letter-spacing: 5px;
}

#video_box div:nth-child(3) {
    margin: 3rem 0;
}

/* video_box 끝 */

/* container 시작 버튼 content랑 같아서 같이 맞춤 */
#container {
    position: relative;
    width: 100%;
    margin: 0 auto;
    background: url(../public/img/ce_back.jpg)no-repeat center center;
    background-size: cover;
    background-color: #ccc;
}

.back {
    width: 100%;
    height: 100%;
    padding: 14rem 0;
    background-color: rgba(0, 0, 0, 0.5);
    -webkit-backdrop-filter: blur(1px);
    backdrop-filter: blur(1px);
}

.back>hgroup {
    color: #fff;
}

#container_box {
    width: 60%;
    margin: 0 auto;
}

/* container 끝 */

/* news 시작 */
#news {
    width: 70%;
    margin: 8rem auto 0;
}

.news_box {
    margin: 6rem 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 180px;
    grid-gap: 30px;
}

.news_box li {
    border: 1px solid #ccc;
}

.news_box li a {
    position: relative;
    display: block;
    padding-top: 55px;
    padding-left: 5px;
    height: 100%;
}

.news_box li a::after {
    position: absolute;
    top: 20px;
    left: 0;
    content: "";
    display: block;
    width: 40px;
    height: 3px;
    background: #0A253E;
    transition: all 0.5s ease;
}

.news_box li a:hover::after {
    width: 80px;
}

.news_box li a span:nth-child(1) {
    line-height: 1.3;
}

.news_box li a span:nth-child(2) {
    position: absolute;
    right: 10px;
    bottom: 10px;
    font-size: 0.9rem;
    color: #999;
}
/* news 끝 */

/* blog 시작 */
#blog {
    width: 100%;
    margin: 7rem auto 0;
    background: url(../public/img/brback.png) no-repeat bottom center;
    background-size: 100%;
    padding-bottom: 4rem;
}

.blog_box {
    width: 60%;
    margin: 7rem auto ;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 20px;
}

.blog_box>li {
    background: #fff;
    border: 1px solid #ccc;
    border-radius: 30px 0 ;
    overflow: hidden;
    box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.2);
}

.blog_box>li a>img {
    width: 100%;
}

.review {
    position: relative;
    margin-bottom: 2rem;
}

.review li {
    margin-top: 10px;
    margin-left: 10px;
}

.review li:nth-child(1) {
    display: flex;
    justify-content: start;
    align-items: center;
}

.review li span {
    display: block;
    height: 25px;
    line-height: 25px;
}

.review li span:nth-child(3) {
    position: absolute;
    right: 10px;
    border: 1px solid #00c73c;
    width: 70px;
    text-align: center;
    font-size: 0.9rem;
    color: #00c73c;
    transition: all 0.5s ease;
}

.review li span:nth-child(3):hover {
    border: 1px solid #00c73c;
    background: #00c73c;
    color: #fff;
}

.review img {
    width: 40px;
    height: 40px;
    border-radius: 40px;
    margin-right: 10px;
}

#blog p>a {
    display: block;
    border: 1px solid #000;
    width: 150px;
    margin: 3.5rem auto 0;
    height: 40px;
    line-height: 40px;
    text-align: center;
    transition: all 0.4s ease;
}

#blog p>a:hover {
    border: 1px solid #00c73c;
    background: #00c73c;
    color: #fff;
}
/* blog 끝 */

/* quick 시작 */
#quick {
    position: absolute;
    top: 300px;
    right: 0;
    width: 80px;
    height: 500px;
    border: 1px solid #ccc;
    background: #fff;
}

#quick ul {
    width: 70%;
    margin: 1.4rem auto;
    text-align: center;
    display: grid;
    grid-template-rows: repeat(5, 0.5fr);
    grid-row-gap: 30px;
}

#quick span {
    display: block;
    font-size: 0.8rem;
    margin-top: 10px;
}

#quick img {
    height: 37px;
}
/* quick 끝 */

/* footer 시작 */
footer {
    background: #333;
    width: 100%;
    padding: 2rem 0 4rem ;
}

.foot_box {
    position: relative;
    width: 80%;
    margin: 0 auto;
}

.foot_navi {
    display: flex;
    justify-content: end;
    border-bottom: 2px solid #CCC;
    padding-bottom: 2rem;
}

.foot_navi li a {
    color: #fff;
    display: block;
    height: 25px;
    line-height: 25px;
    margin-left: 10px;
    font-size: 0.9rem;
}

.foot_navi img {
    width: 25px;
}

footer h2 {
    position: absolute;
    left: 0;
    top: 6rem;
}

footer h2 img {
    width: 150px;
}

.foot_text {
    width: 70%;
    margin: 2.5rem auto 0;
    color: #fff;
}

.foot_text li {
    font-size: 0.8rem;
    margin-bottom: 7px;
}
/* footer 끝 */